body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;

  margin: 5px;
}

.status {
  margin-bottom: 10px;
}

.square {
  border-radius: 20px;
  background: #6a2c70;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: 5px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.X-Player {
  font-size: 80px;
  color: #f9ed69;
}

.O-Player {
  font-size: 80px;
  color: #f08a5d;
}
.moves {
}

.moves--element {
  width: 300px;
  height: 40px;
  margin-bottom: 10px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-board {
  border-radius: 30px;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.game-info {
  margin-left: 20px;
}
